@font-face{font-family:"Ferry";src:url("../public/fonts/ferry_black.otf")}*{box-sizing:border-box;margin:0;font-family:"Inter",Arial,Helvetica,sans-serif}body{min-width:1440px}.root{display:flex;flex-direction:column;min-height:100vh}main{flex-grow:1}.container{max-width:1360px;margin:0 auto;padding:0 20px}.h{font-family:"Ferry",Tahoma,Verdana,sans-serif}.h1{font-size:60px}.h2{font-size:45px}.h3{font-size:40px}.h4{font-size:30px}.h5{font-family:"Inter",Arial,Helvetica,sans-serif;font-size:30px}.h6{font-family:"Inter",Arial,Helvetica,sans-serif;font-size:26px;font-weight:600}@keyframes rotate{0%{transform:rotate(0deg)}40%{transform:rotate(180deg)}60%{transform:rotate(220deg)}100%{transform:rotate(360deg)}}.spinner img{width:40px;height:40px;animation:rotate 2s infinite linear}@media screen and (max-width: 900px){body{min-width:900px}.container{min-width:900px}.h1{font-size:40px}.h2{font-size:35px}.h3{font-size:28px}.h4{font-size:25px}}@media screen and (max-width: 600px){body{min-width:500px}.container{min-width:500px}.h1,.h2{font-size:28px}.h3{font-size:22px}.h4{font-size:20px}}.hom_sec3_tariffs{display:flex;justify-content:space-between;margin:0 0 118px;font-size:18px}.hom_sec3_tariffs .current{border:2px solid var(--background)}.hom_sec3_tariffs .tarif{width:32%;box-shadow:0 0 20px rgba(0,0,0,.2);border-radius:10px;overflow:hidden;margin-top:70px}.hom_sec3_tariffs .tarif .sec3_h5{margin-bottom:10px}.hom_sec3_tariffs .tarif .title{display:flex;justify-content:space-between;color:var(--color);background-color:var(--background)}.hom_sec3_tariffs .tarif .title .title_text{margin:30px 0 34px 30px}.hom_sec3_tariffs .tarif .title .img{margin:11px 15px 0 0}.hom_sec3_tariffs .tarif .currentTariff{height:24px;margin-top:12px;display:flex;justify-content:right}.hom_sec3_tariffs .tarif .currentTariff .badge{color:#fff;background-color:#3ba5e0;border-radius:10px;height:inherit;padding:0 13px;margin-right:10px;font-size:14px;vertical-align:middle;display:flex;align-items:center}.hom_sec3_tariffs .tarif .text{margin:0 15px 0 30px}.hom_sec3_tariffs .tarif .text .oldPrice{opacity:50%;text-decoration:line-through;font-size:25px}.hom_sec3_tariffs .tarif .ul{padding:0;margin-top:59px}.hom_sec3_tariffs .tarif .ul .ul_title{font-size:20px;font-weight:bold;margin-bottom:10px}.hom_sec3_tariffs .tarif .ul .li{list-style-type:none}.hom_sec3_tariffs .tarif .ul .li:before{content:url(images/greenСheckmark.png);width:20px;height:20px;margin-right:8px;vertical-align:middle}.hom_sec3_tariffs .tarif .buttonTariff{display:flex;justify-content:center;align-items:center;text-decoration:none;background-color:#5970ff;width:calc(100% - 60px);height:59px;margin:55px 30px 24px;border:none;border-radius:5px;color:#fff;font-size:20px;cursor:pointer}.hom_sec3_tariffs .tarif .buttonCurrent{background-color:#d2d2d2;color:#000}@media screen and (max-width: 900px){.hom_sec3_tariffs{flex-direction:column;margin:0 0 43px}.hom_sec3_tariffs .tarif{width:60%;margin:37px auto}.hom_sec3_tariffs .tarif .title{min-height:auto}.hom_sec3_tariffs .tarif .title .title_text{max-width:300px}.hom_sec3_tariffs .tarif .ul{min-height:auto}.hom_sec3_tariffs .tarif .installment{max-width:400px}.hom_sec3_tariffs .tarif .buttonTariff{margin:35px 30px 24px}}@media screen and (max-width: 600px){.hom_sec3_tariffs .tarif{width:100%}}