@font-face{font-family:"Ferry";src:url("../public/fonts/ferry_black.otf")}*{box-sizing:border-box;margin:0;font-family:"Inter",Arial,Helvetica,sans-serif}body{min-width:1440px}.root{display:flex;flex-direction:column;min-height:100vh}main{flex-grow:1}.container{max-width:1360px;margin:0 auto;padding:0 20px}.h{font-family:"Ferry",Tahoma,Verdana,sans-serif}.h1{font-size:60px}.h2{font-size:45px}.h3{font-size:40px}.h4{font-size:30px}.h5{font-family:"Inter",Arial,Helvetica,sans-serif;font-size:30px}.h6{font-family:"Inter",Arial,Helvetica,sans-serif;font-size:26px;font-weight:600}@keyframes rotate{0%{transform:rotate(0deg)}40%{transform:rotate(180deg)}60%{transform:rotate(220deg)}100%{transform:rotate(360deg)}}.spinner img{width:40px;height:40px;animation:rotate 2s infinite linear}@media screen and (max-width: 900px){body{min-width:900px}.container{min-width:900px}.h1{font-size:40px}.h2{font-size:35px}.h3{font-size:28px}.h4{font-size:25px}}@media screen and (max-width: 600px){body{min-width:500px}.container{min-width:500px}.h1,.h2{font-size:28px}.h3{font-size:22px}.h4{font-size:20px}}.wrapperCheckbox{margin:8px 0 15px}.wrapperCheckbox .checkbox{position:absolute;z-index:-1;opacity:0}.wrapperCheckbox .labelCheckbox{display:inline-flex;user-select:none;height:19px}.wrapperCheckbox .labelCheckboxDisabled{color:rgba(0,0,0,.4)}.wrapperCheckbox .labelCheckbox::before{content:"";display:inline-block;width:18px;height:18px;flex-shrink:0;flex-grow:0;border:1px solid #000;margin-right:17px;background-repeat:no-repeat;background-position:center center;background-size:50% 50%;background-size:contain}.wrapperCheckbox .labelCheckboxDisabled::before{border:1px solid rgba(0,0,0,.4)}.wrapperCheckbox .checkbox:checked+.labelCheckbox::before{background-image:url(../../../home/tariffs/images/greenСheckmark.png)}.wrapperCheckbox .checkbox:checked+.labelCheckboxDisabled::before{background-image:none}